<template>
  <div class="inner">
    <ul id="tabs">
      <router-link tag="li" to="/home">
        首页
      </router-link>
      <router-link tag="li" to="/rank">
        排行
      </router-link>
      <router-link tag="li" to="/smog">
        雾霾
      </router-link>
      <router-link tag="li" to="/history">
        天气历史
      </router-link>
      <router-link tag="li" to="/smogHistory">
        空气历史
      </router-link>
    </ul>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {}
</script>

<style lang="less" scoped rel="stylesheet/less" type="text/less">
  @media screen and(max-width: 414px) {
    .inner {
      width: 95% !important;
    }
  }

  .inner {
    position: absolute;
    left: 0;
    right: 0;
    margin: 50px auto 0;
    z-index: 10;
    #tabs {
      box-sizing: border-box;
      padding: 0 30px;
      width: 100%;
      height: 50px;
      line-height: 50px;
      text-align: center;
      border-radius: 25px;
      display: flex;
      justify-content: flex-start;
      flex-wrap: nowrap;
      background-color: rgba(0, 0, 0, .5);
      color: #fff;
      li {
        padding: 0 0.3rem;
        font-size: 16px;
        cursor: pointer;
        transition: all 0.3s;
        &:hover {
          color: #00BFFF;
        }
        &.router-link-active {
          color: #00BFFF;
        }
      }
    }
  }
</style>



