import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

const Home = (resolve) => {
  import('components/home/home').then((recommend) => {
    resolve(recommend)
  })
}
const Rank = (resolve) => {
  import('components/rank/rank').then((recommend) => {
    resolve(recommend)
  })
}
const Smog = (resolve) => {
  import('components/smog/smog').then((recommend) => {
    resolve(recommend)
  })
}
const History = (resolve) => {
  import('components/history/history').then((recommend) => {
    resolve(recommend)
  })
}
const HistoryDetail = (resolve) => {
  import('components/historyDetail/historyDetail').then((recommend) => {
    resolve(recommend)
  })
}

const SmogHistory = (resolve) => {
  import('components/smogHistory/smogHistory').then((recommend) => {
    resolve(recommend)
  })
}
const SmogHistoryDetail = (resolve) => {
  import('components/smogHistoryDetail/smogHistoryDetail').then((recommend) => {
    resolve(recommend)
  })
}

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/home',
      component: Home
    },
    {
      path: '/rank',
      component: Rank
    },
    {
      path: '/smog',
      component: Smog
    },
    {
      path: '/history',
      component: History
    },
    {
      path: '/historyDetail',
      component: HistoryDetail
    },
    {
      path: '/smogHistory',
      component: SmogHistory
    },
    {
      path: '/smogHistoryDetail',
      component: SmogHistoryDetail
    }
  ],
  scrollBehavior (to, from, savedPosition) {
    return {x: 0, y: 0}
  }
})



