import * as types from './mutation-type'

const mutations = {
  [types.SET_SMOG_CITY](state, smogCity) {
    state.smogCity = smogCity
  },
  [types.SET_SMOG_HISTORY](state, smogHistory) {
    state.smogHistory = smogHistory
  },
  [types.SET_WEATHER_HISTORY](state, weatherHistory) {
    state.weatherHistory = weatherHistory
  }
}

export default mutations



